.container {
  position: relative;
}

.coordsContainer {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 150px;
  padding: 10px;
  color: white;

  background-color: rgba(59, 61, 63, 0.856);
}

.coords {
  align-self: center;
  margin-bottom: -20px;
}

.coordsContainerTooltip {
  margin-right: auto;
  font-size: 25px;
  position: relative;
  display: inline-block;
  cursor: default;
}

.coordsContainerTooltip .coordsContainerTooltipText {
  visibility: hidden;
  padding: 0.25em 0.5em;
  background-color: rgba(59, 61, 63, 0.856);
  color: #fff;
  text-align: left;
  border-radius: 0.25em;
  width: 200px;
  font-size: 16px;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: 0%;
  right: 35px;
  transition-property: visibility;
  transition-delay: 0s;
}

.coordsContainerTooltip:hover .coordsContainerTooltipText {
  visibility: visible;
}

.setButtons {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  gap: 5px;
}

.setButtons button {
  width: 100%;
}

.imgContainer {
  position: relative;
  display: inline-block;
}

.guessAreaMarker {
  display: flex;
  justify-content: center;
  align-items: center;

  border-style: dashed;
  border-width: 4px;
  border-color: rgb(211, 255, 167);
  color: rgb(189, 255, 122);

  width: 100px;
  height: 100px;

  cursor: default;
}

.uploadContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  color: white;
}

.uploadImage {
  color: white;
  margin-left: 35px;
  margin-bottom: 10px;
}

.signInMessage {
  color: rgb(248, 175, 175);
  margin-bottom: 5px;
  font-weight: bold;
}

.levelInfoInput {
  background-color: rgb(230, 230, 230);
  border-style: solid;
  border-color: gray;
  border-width: 1px;
  border-radius: 5px;
}

.levelInfoBtn {
  cursor: pointer;
  margin-bottom: 15px;
  background-color: rgb(133, 133, 133);
  border: 1px solid rgb(133, 133, 133);
  border-radius: 5px;
}

.levelInfoBtn:hover {
  background-color: rgb(167, 166, 166);
}
